import uniSwipeAction from "@/components/uni-swipe-action/uni-swipe-action.vue";
import uniBadge from "@/components/uni-badge/uni-badge.vue"
import util from '@/common/utils'
import config from '@/common/configs'
import {registerWyAccount,getInfo} from "@/api/api.js"
export default {
	data() {
		return {
			delSessionId: null,
			options: [{
				text: '刪除',
				style: {
					backgroundColor: '#dd524d'
				}
			}],
			targetId: '', // 你要给谁发送消息 目标ID
			showDatas: [], // 初始化信息
			convrList: ""
		}
	},
	components: {
		uniSwipeAction,
		uniBadge
	},
	computed: {
		sessionlist() {
			let sessionlist = this.$store.state.sessionlist.filter(item => {
				item.name = '';
				item.avatar = '';
				if (item.scene === 'p2p') {
					let userInfo = null
					getInfo(item.lastMsg.target).then(data=>{
						item.name = data.name
						item.avatar =data.portrait
					})
				}
				let lastMsg = item.lastMsg || {}
				if (lastMsg.type === 'text') {
					item.lastMsgShow = lastMsg.text || ''
				} else if (lastMsg.type === 'custom') {
					item.lastMsgShow = util.parseCustomMsg(lastMsg)
				} else if (lastMsg.scene === 'team' && lastMsg.type === 'notification') {
					item.lastMsgShow = util.generateTeamSysmMsg(lastMsg)
				} else if (util.mapMsgType(lastMsg)) {
					item.lastMsgShow = `[${util.mapMsgType(lastMsg)}]`
				} else {
					item.lastMsgShow = ''
				}
				if (item.updateTime) {
					item.updateTimeShow = util.formatDate(item.updateTime, true)
				}
				return item
			})
			console.log(sessionlist)
			sessionlist.find(function(){
				
			})
			return sessionlist
		}
	},

	methods: {
		enterChat(e) {
			uni.navigateTo({
				url: 'chat?id='+e ,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		},
		bindClick(id) {
			console.log('用户点击刪除');
			this.$store.dispatch('deleteSession', id)
		},
		pickerChange(e) {
			console.log('picker发送选择改变，携带值为：' + e.target.value)
		},
		toTset() {
			uni.navigateTo({
				url: "test"
			})
		},
		toReceive() {
			uni.navigateTo({
				url: "Receive"
			})
		}
	},
	onPullDownRefresh() {
		console.log("onPullDownRefresh")
		setTimeout(function() {
			uni.startPullDownRefresh()
		}, 500)
	},
	onNavigationBarButtonTap() {
		uni.showActionSheet({
			itemList: ['忽略未读提醒', '清空聊天列表', '提醒设置'],
			success: function(res) {
				console.log('选中了第' + (res.tapIndex) + '个按钮');
				if (res.tapIndex == 2) {
					uni.navigateTo({
						url: '/pagesA/settingChat/settingChat'
					});
				}
				if (res.tapIndex == 0) {
					uni.showModal({
						title: '',
						content: '确定忽略未读提醒',
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				};
				if (res.tapIndex == 1) {
					uni.showModal({
						title: '',
						content: '确定清空聊天列表',
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
			},
			fail: function(res) {
				console.log(res.errMsg);
			}
		});

	},
}
